.mf-header {
  display: flex;
  height: 48px;
  background: #fff;
}
.mf-header .mf-header__title {
  height: 100%;
  flex: 1 1;
  display: flex;
  font-weight: bold;
  font-size: 18px;
  color: #333333;
  align-items: center;
  justify-content: flex-start;
  padding-left: 20px;
}
.mf-header .mf-header__info {
  flex: 3 1;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding-right: 20px;
  font-size: 14px;
  color: #666666;
}
.mf-header .iconfont {
  margin-right: 5px;
}
