.mf-menu {
  background: #3F456C;
  display: flex;
  flex-direction: column;
  position: relative;
  width: 240px;
  height: 100%;
}
.mf-menu.mf-menu--wider {
  width: 400px;
}
.mf-menu .mf-menu__corpinfo {
  height: 200px;
  width: 240px;
  padding-top: 60px;
  margin-bottom: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.mf-menu .mf-menu__corpinfo__logo {
  width: 72px;
  height: 72px;
  background: #eee;
  border-radius: 72px;
  margin-bottom: 10px;
}
.mf-menu .mf-menu__dropdown__link {
  font-size: 16px;
  position: relative;
  color: #FFFFFF;
  width: 100%;
  display: flex;
  justify-content: center;
}
.mf-menu .mf-menu__dropdown__link .iconfont {
  position: absolute;
  right: 20px;
  top: 0px;
}
.mf-menu .mf-menu__dropdown__title {
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 180px;
}
.mf-menu .mf-menu__nav .mf-menu__nav__item {
  width: 240px;
  height: 100px;
}
.mf-menu .mf-menu__nav .mf-menu__item__link {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  color: #D5D7EB;
}
.mf-menu .mf-menu__nav .mf-menu__item__link .iconfont {
  font-size: 32px;
  margin-bottom: -10px;
}
.mf-menu .mf-menu__nav .mf-menu__item__link:hover {
  background: #363b5c;
}
.mf-menu .mf-menu__nav .mf-menu__item__link.active {
  color: #658FFF;
  background: #2A2C43;
  -webkit-transition: 0.3;
  transition: 0.3;
  opacity: 1;
}
.mf-menu .mf-menu__nav .mf-menu__item__link.active:hover {
  background: #2A2C43;
}
.mf-menu__item__link.active .mf-submenu__nav {
  display: block;
  width: 160px;
  overflow: hidden;
}
.mf-menu__item__link .mf-submenu__nav {
  display: block;
  overflow: hidden;
  width: 0px;
  position: absolute;
  left: 240px;
  top: 0;
  bottom: 0;
  background: #EFEFEF;
  padding-top: 48px;
}
.mf-menu__item__link .mf-submenu__nav__item__link {
  display: block;
  height: 48px;
  line-height: 48px;
  color: #333333;
  text-align: left;
  padding-left: 30px;
  margin-bottom: 20px;
}
.mf-menu__item__link .mf-submenu__nav__item__link.active {
  background: #fff;
  color: #6186FF;
}
.mf-menu__item__link .mf-submenu__nav__item__link:hover {
  background: #fcfcfc;
}
